Body {
  text-align: center;
  position: relative;
  align-content: center;

}

#imdiv {
  position: inherit;
  align-content: center;
}
#bio {
  text-align: justify;
  color: #F0F2F0
}

#row {
  justify-content: space-between;
}

.texty {
  color: #F0F2F0;
  font-weight: bold;
  font-size: 33px;
}

#contents {
  align-items: center;
  float: right;
}
#image {
  float: right;
  height: 150;
}

.banner-text {
  margin: auto;
  color: #F0F2F0;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
}

.banner-text h3{
  font-weight: bold;
  font-size: 33px;
}

.banner-text hr{
  border-top: 2.5px dotted #F0F2F0;
  width: 50%;
  margin: auto;
}

.banner-text p{
  font-weight: bold;
  padding: 1em;
}

.social-links {
  color: #F0F2F0;
  justify-content: space-between;
  margin: auto;
  width: 50%;
}

.social-links i {
  color: #F0F2F0;
  justify-content: space-between;
}

.social-links i :hover{
  color: #000C40;
}

.Body {
  margin-top: 5px;
}
.portcard {
  border: 1px solid white; 
  /* overflow: auto !important; */
  margin: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.portImg {
  display: block !important;
  margin-left: auto !important;
  margin-right: auto !important;
  width: 95% !important;
  overflow: auto !important;
  border: 2px solid #343a40;
}

.projecttext {
  text-align: center;
  font-size: 18px;
}

.portcard hr{
  border-top: 2.5px dotted #F0F2F0;
  width: 50%;
  margin: auto;
}
#kard {
  border: solid white 1px;
}


footer {
  color: #F0F2F0;
  /* border-top: 1px solid #000C40; */
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.footer-copyright {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}
#root {
  background: #f0f2f0; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #000c40,
    #f0f2f0
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  min-height: 100vh;
  background-size: cover;
  background-position: center;
}

